import React from "react";
import { Grid, CircularProgress, Typography, Hidden, Divider } from "@material-ui/core";
import RankingCard, { RankProps } from "./RankingCard";
import API from "../../Services/API";
import LineCard from "./LineCard"
import ProfitCard from "./ProfitCard";
import PieCard from "./PieCard";
import BardCard from "./BarCard";
import WithTimeTravel from "./WithTimeTravel";
import { ROLES } from "../../Globals";

const TimeTravelRank = WithTimeTravel<RankProps>(RankingCard);
const PersonalGraph = WithTimeTravel(LineCard);

export default class Feed extends React.Component<{ onMounted: Function, grupo: string, username: string, rol: number }> {
    unmounted = false;
    state = {
        loading: true,
        error: null,
        week_ranking: [],
        group_graph: [],
        personal_graph: [],
        year_sales: [],
        product_resume: [],
        group_ranking: [],
        current_week: null
    }
    constructor(props) {
        super(props);
        props.onMounted(props.match.path);
    }
    componentDidMount() {
        const stats = [
            "week_ranking",
            "personal_graph",
            "year_sales",
            "product_resume",
            "current_week"
        ];
        if (this.props.rol !== ROLES.ADMINISTRADOR) stats.push("group_graph");
        if (this.props.rol === ROLES.ADMINISTRADOR) stats.push("group_ranking");
        API.Stats(stats)
            .then(x => {
                if (this.unmounted) return;
                this.setState({ ...x, loading: null })
            })
            .catch(error => {
                if (this.unmounted) return;
                this.setState({ error, loading: null });
            });
    }
    componentWillUnmount = () => this.unmounted = true;
    productResumeReducer = data => data.map(a => {
        a.username = a.label;
        return a;
    });
    render() {
        var { loading, error,
            week_ranking,
            personal_graph,
            group_graph,
            year_sales,
            product_resume,
            group_ranking,
            current_week
        } = this.state;
        const { rol, grupo, username } = this.props;
        var resume = { month: 0, year: 0 };
        var ahora = new Date();
        const esAdmin = rol === ROLES.ADMINISTRADOR;

        for (let i = 0; i < year_sales.length; i++) {
            const x = year_sales[i];
            x.createdAt = new Date(x.createdAt);
            if (x.createdAt.getMonth() === ahora.getMonth() &&
                x.createdAt.getFullYear() === ahora.getFullYear()) {
                resume.month += x.profit;
            }
            resume.year += x.profit;
        }

        if (loading) return (<div style={{ width: "100%", paddingTop: 100, textAlign: "center" }}>
            <CircularProgress size={100} />
            <Typography variant="h6">CARGANDO...</Typography>
        </div>);

        if (error) return (<div style={{ width: "100%", paddingTop: 100, textAlign: "center" }}>
            <Typography variant="h5">OCURRIÓ UN PROBLEMA</Typography>
            <Typography variant="subtitle1">{error.message}</Typography>
            <Typography variant="body1">{error.status} {error.code}</Typography>
        </div>);

        return (<Grid container spacing={2} style={{ margin: "auto", width: "calc(100% - 10px)", textAlign: "center" }}>

            <Grid item xs={12}>
                <Typography color="primary" variant="h6">
                    Hola {username}, Esta es tu cuenta personal de Natú.
                </Typography>
                <Typography variant="subtitle1">
                    Estamos aquí para prosperar juntos. Estos son los indicadores de tu trabajo.
                </Typography>
            </Grid>

            {(group_ranking && group_ranking.length)
                ? <Grid item sm={6} xs={12}>
                    <TimeTravelRank
                        statName="group_ranking"
                        data={group_ranking}
                        current_week={current_week}
                        title="RANKING DE GRUPOS"
                        colType="MONEY"
                        secondColType="MONEY"
                        col1="stat"
                        col2="ventas_lider"
                    />
                </Grid>
                : null}

            <Grid item sm={6} xs={12}>
                <PersonalGraph
                    data={personal_graph}
                    statName="personal_graph"
                    current_week={current_week}
                    title={"ESTADISTICA SEMANAL"}
                />
            </Grid>


            {(group_graph && group_graph.length)
                ? <React.Fragment>
                    <Grid item sm={6} xs={12}>
                        <BardCard data={group_graph} title={grupo || "ESTADISTICA MENSUAL"} />
                    </Grid>

                    <Grid item sm={6} xs={12}>
                        <PieCard data={product_resume} title="PRODUCTOS VENDIDOS" />
                    </Grid>

                    <Grid item sm={6} xs={12}>
                        <ProfitCard month={resume.month} year={resume.year} />
                    </Grid>
                </React.Fragment>
                : null}

            <Hidden smUp>
                <Grid item xs={12}>
                    <Divider />
                </Grid>
            </Hidden>

            {esAdmin && <Grid item sm={6} xs={12}>
                <TimeTravelRank
                    current_week={current_week}
                    statName="product_resume"
                    reduceFn={this.productResumeReducer}
                    data={product_resume}
                    title="PRODUCTOS VENDIDOS"
                    colType="NUMBER"
                    col1="value"
                />
            </Grid>}
            <Grid item sm={6} xs={12}>
                <TimeTravelRank
                    statName="week_ranking"
                    current_week={current_week}
                    data={week_ranking}
                    reduceFn={data => esAdmin ? data.filter(x => x.rol === ROLES.ADMINISTRADOR) : data}
                    title="RANKING DE LA SEMANA"
                    colType="MONEY"
                    secondColType="DATE"
                    col1="stat"
                    col2="ultimoPedido"
                />
            </Grid>

            {!esAdmin && <Grid item sm={6} xs={12}>
                <TimeTravelRank
                    statName="week_ranking"
                    current_week={current_week}
                    data={week_ranking}
                    title="PROGRESO DE LA SEMANA"
                    colType="PERFOMANCE"
                    col1="perfomance"
                />
            </Grid>}

            {rol !== ROLES.ASESOR && <Grid item sm={6} xs={12}>
                <RankingCard
                    data={week_ranking}
                    title="RANKING ACUMULATIVO"
                    colType="MONEY"
                    col1="monto_acumulativo"
                />
            </Grid>}

        </Grid>);
    }
}