import React from "react";
import API from "../../Services/API";
import ProductGrid from "./ProductGrid";
import { Typography, CircularProgress } from "@material-ui/core";
import { Route } from "react-router-dom";
import Edit from "./Edit";

export default class extends React.Component<any, any> {
    state = {
        data: [] as Product[],
        loading: true,
        error: null
    }

    componentDidMount() {
        if (this.props.onMounted) this.props.onMounted(this.props.match.path);
        this._Update();
    }

    _Update = () => {
        API.GetProducts(true)
            .then(data => this.setState({ data, loading: false }))
            .catch(error => this.setState({ loading: false, error }));
    }
    handleClick = e => {
        this.props.history.push(this.props.match.path + "/" + e.currentTarget.value);
    }
    handleDone = shouldUpdate => {
        if (shouldUpdate) this._Update();
        this.props.history.push(this.props.match.path);
    }
    productSorted() {
        return this.state.data.sort((a, b) => a.orden - b.orden);
    }
    handleAction = event => {
        const { name, value } = event.currentTarget;
        var sorted = this.productSorted();
        var exists = sorted.find(x => x.id === +value);
        // NO EXISTE
        if (!exists) return;
        // PRIMERO
        if (name === "LEFT" && sorted.indexOf(exists) === 0) return;
        // ULTIMO
        if (name === "RIGHT" && sorted.length === (sorted.indexOf(exists) + 1)) return;

        API.SendProductAction(value, name)
            .then(this._Update)
            .catch(error => this.setState({ error }));
    }
    render() {
        if (this.state.loading)
            return (<div style={{ width: "100%", textAlign: "center", paddingTop: 100 }} >
                <CircularProgress size={100} />
                <Typography variant="h6">CARGANDO...</Typography>
            </div>);

        return (<div style={{ width: "calc(100% - 25px)", margin: "auto", marginTop: 10 }}>
            <ProductGrid
                items={this.productSorted()}
                showNewItem={true}
                visible={true}
                controls="LEFT-RIGHT"
                onActionClick={this.handleAction}
                onCardClick={this.handleClick}
            />
            <Route
                path={this.props.match.path + "/:id"}
                children={({ match }) => (
                    <Edit
                        productList={this.state.data}
                        showingID={match && match.params.id}
                        onDone={this.handleDone}
                    />
                )}
            />
        </div>);
    }
}