import React from "react";
import PolicyPage from "../Components/PolicyPage";
import { Button, FormControlLabel } from "@material-ui/core";
import API from "../Services/API";

export default class extends React.Component<{
    onDone: () => void
}> {
    state = {
        checked: false,
        loading: false
    }
    constructor(props) {
        super(props);
        this.handleCheck = this.handleCheck.bind(this);
        this.handleClick = this.handleClick.bind(this);
    }
    handleCheck(event) {
        const checked = event.currentTarget.checked;
        this.setState({ checked });
    }
    handleClick(event) {
        if (this.state.loading) return;
        if (!this.state.checked) return;
        this.setState({ loading: true });

        API.SendUserAction("ACCEPT_TERMS_AND_CONDS")
            .then(this.props.onDone)
            .catch(ex => {
                alert(ex.message);
                this.setState({ loading: false });
            });
    }
    render() {
        const { checked, loading } = this.state;
        return (<div className="full-height" style={{ display: "flex", flexDirection: "column" }}>
            <PolicyPage
                className="iframe-wrapper-with-max-height"
                TextSrc="https://docs.google.com/document/d/e/2PACX-1vQM7gflRmudx9mnIJpfZMu5gM3WUl3u_h8RRuyVbb6yErxWTnU_jeu8kcKUJhht3VadenapB4nINf7h/pub?embedded=true"
            />
            <div style={{
                margin: "auto",
                textAlign: "center",
                paddingTop: 10,
                paddingBottom: 10,
                width: "100%"
            }}>
                <FormControlLabel
                    control={<input
                        disabled={loading}
                        checked={checked}
                        type="checkbox"
                        onChange={this.handleCheck}
                    />}
                    label="Acepto los términos y condiciones"
                />
                <Button
                    disabled={loading || !checked}
                    color="primary"
                    variant="contained"
                    onClick={this.handleClick}
                >
                    CONTINUAR
                </Button>
            </div>
        </div>);
    }
}