import React from "react";

export default class extends React.PureComponent<{
    TextSrc: string;
    onMounted?: (path: any) => void;
    path?: string;
    className?: string;
}> {
    componentWillMount() {
        if (this.props.onMounted) this.props.onMounted(this.props.path);
    }
    render() {
        return (
            <div id="Iframe-Liason-Sheet" className={"iframe-border center-block-horiz " + (this.props.className || "")}>
                <div className="responsive-wrapper responsive-wrapper-wxh-760x1200">
                    <iframe
                        title="Politicas de Natú"
                        src={this.props.TextSrc}
                    >Cargando...</iframe>
                </div>
            </div>
        );
    }
}