import React from "react";
import { FormLabel, InputAdornment, TextField, IconButton, Typography, Button, CircularProgress, FormControlLabel, Switch } from "@material-ui/core";
import { Visibility, VisibilityOff } from "@material-ui/icons";

interface Props {
    loading: boolean;

    id?: number;
    nombre: string;
    identificacion: string;
    telefono: string;
    email: string;
    nacionalidad: string;
    ciudad: string;
    fechaNacimiento: string;
    direccion: string;
    username: string;
    password: string;
    confirm_password: string;
    grupo: string;
    grupos: { id: string, nombre: string }[];

    rol?: number;
    editarPVPsdurantePedido?: boolean;
    estado?: string;

    mustAcceptTermsAndConds?: boolean;
    termsAndCondsAcceptedAt?: Date;

    onChange: (event: React.ChangeEvent<HTMLInputElement>) => void;
    onSubmit: () => void;
    formType: "SIGN-IN" | "EDIT"
    error?: any;
}

export default function (props: Props) {

    const { loading, id, error, grupos, onSubmit, onChange, formType, ...state } = props;

    function _onSubmit(event) {
        event.preventDefault();
        event.stopPropagation();
        onSubmit();
    }
    return (<form noValidate onSubmit={_onSubmit}>
        <Typography variant="h6">Información Personal</Typography>
        <Input disabled={loading} error={error && error.nombre} onChange={onChange} value={state.nombre || ""} name="nombre" label="Nombres Completos" />
        <Input disabled={loading} error={error && error.identificacion} onChange={onChange} value={state.identificacion || ""} name="identificacion" label="Identificacion (CI o Pasaporte)" type="phone" />
        <Input disabled={loading} error={error && error.telefono} onChange={onChange} value={state.telefono || ""} name="telefono" label="Telefono" type="phone" />
        <Input disabled={loading} error={error && error.email} onChange={onChange} value={state.email || ""} name="email" label="Correo Electronico" type="email" />
        <Input disabled={loading} error={error && error.nacionalidad} onChange={onChange} value={state.nacionalidad || ""} name="nacionalidad" label="Nacionalidad" />
        <Input disabled={loading} error={error && error.fechaNacimiento} onChange={onChange} value={state.fechaNacimiento || ""} name="fechaNacimiento" label="Fecha Nacimiento" type="date" />

        <Typography variant="h6" style={{ marginTop: 30 }}>{formType === "SIGN-IN" ? "Agrega tu dirección" : "Dirección"}</Typography>
        <Input disabled={loading} error={error && error.ciudad} onChange={onChange} value={state.ciudad || ""} name="ciudad" label="Ciudad" />
        <Input disabled={loading} error={error && error.direccion} onChange={onChange} value={state.direccion || ""} name="direccion" label="Direccion" />

        <Typography variant="h6" style={{ marginTop: 30 }}>{formType === "SIGN-IN" ? "Crea tu Usuario" : "Usuario"}</Typography>
        <Input disabled={loading} error={error && error.username} onChange={onChange} value={state.username || ""} name="username" label="Usuario" />
        <Password disabled={loading} error={error && error.password} onChange={onChange} value={state.password || ""} name="password" label="Contraseña" />
        <Password disabled={loading} error={error && error.confirm_password} onChange={onChange} value={state.confirm_password || ""} name="confirm_password" label="Confirma tu contraseña" />

        <Typography variant="h6" style={{ marginTop: 30 }} >{formType === "SIGN-IN" ? "A qué grupo quieres pertenecer?" : "Configuración del Usuario"}</Typography>

        {formType === "EDIT" && id
            ? (<div style={{ marginTop: 10, marginBottom: 10 }}>
                <Typography style={{ display: "inline" }} >Rol: </Typography>
                <Typography style={{ display: "inline" }} variant="button" >{RolToString(state.rol)}</Typography>
            </div>)
            : (formType === "EDIT" && <Input
                select
                disabled={loading}
                value={state.rol || ""}
                name="rol"
                label="Rol"
                SelectProps={{ native: true }}
                onChange={onChange}
            >
                <option value="1" >{RolToString(1)}</option>
                <option value="2" >{RolToString(2)}</option>
                <option value="3" >{RolToString(3)}</option>
            </Input>)}

        {(+state.rol === 1 || state.rol === undefined || state.rol === null) && <Input select
            disabled={loading}
            error={error && error.grupo}
            onChange={onChange}
            value={state.grupo || ""}
            name="grupo"
            label=""
            SelectProps={{ native: true }}
        >
            <option value="">- SELECCIONA UN GRUPO -</option>
            {grupos.map(g => <option key={g.id} value={g.id} >{g.id} - {g.nombre}</option>)}
        </Input>}

        {+state.rol === 2 && <Input
            name="grupo"
            label="Grupo"
            value={state.grupo || ""}
            onChange={onChange}
            error={error && error.grupo}
            diabled={loading}
            inputProps={{ maxLength: 50 }}
        />}

        {formType === "EDIT" && <Input
            select
            disabled={loading}
            value={(state.estado === undefined || state.estado === null) ? "" : state.estado.toString()}
            name="estado"
            label="Estado"
            SelectProps={{ native: true }}
            onChange={onChange}
        >
            <option value="1" >PENDIENTE</option>
            <option value="0" >APROBADO (Habilitado)</option>
            <option value="2" >RECHAZADO (Deshabilitado)</option>
        </Input>}

        {formType === "EDIT" && +state.rol === 3 &&
            <FormControlLabel
                label="Puede modificar precios durante toma de pedido"
                control={<Switch
                    checked={Boolean(state.editarPVPsdurantePedido)}
                    name="editarPVPsdurantePedido"
                    onChange={onChange}
                />}
            />
        }

        {formType === "EDIT" && <div style={{ margin: "auto", textAlign: "center", paddingTop: 20, paddingBottom: 20 }}>
            <FormControlLabel
                control={<input
                    type="checkbox"
                    onChange={onChange}
                    name="mustAcceptTermsAndConds"
                    checked={state.mustAcceptTermsAndConds || false}
                />}
                label="Mostrar Aceptar Terminos y Condiciones al iniciar"
            />
            {state.termsAndCondsAcceptedAt && <div style={{ marginTop: 10 }}>
                <Typography variant="button">
                    Este usuario ha aceptado los Términos y Condiciones el:
                {new Date(state.termsAndCondsAcceptedAt).toLocaleString()}
                </Typography>
            </div>}
        </div>}


        {(error && error.guardar) ? <Typography tabIndex={0} id="guardar" color="secondary">{error.guardar}</Typography> : null}
        {
            formType === "SIGN-IN" &&
            (<Button type="submit" color="primary" variant="outlined" fullWidth style={{ marginTop: 20 }} >
                {loading ? <CircularProgress size={20} color="inherit" /> : "ENVIAR"}
            </Button>)
        }
    </form >
    );
}

function Input(props: {
    name: string,
    value: any,
    onChange: any,
    label: string,
    type?: string,
    disabled?: boolean,
    error?: any,
    [key: string]: any
}) {
    const { error, type, label, ...other_props } = props;
    var input_props: any = { type, inputProps: { id: props.name } };
    if (error) {
        input_props.error = true;
        input_props.helperText = error;
    }
    if (type === "date")
        return (<div style={{ width: "100%", display: "flex", flexDirection: "row", marginBottom: 5, marginTop: 15 }}>
            <FormLabel style={{ paddingTop: 8, marginRight: 5 }}>{label}</FormLabel>
            <TextField
                required
                {...input_props}
                style={{ flex: 1 }}
                {...other_props}
            >{props.children} </TextField>
        </div>);

    input_props.label = label;

    return (<TextField
        fullWidth
        required
        {...input_props}
        style={{ marginTop: 5, marginBottom: 5 }}
        {...other_props}
    />);
}

function Password(props: any) {
    const { error, ...other_props } = props;
    const [show_password, switchShowPassword] = React.useState(false);
    var input_props: any = { inputProps: { id: props.name } };
    if (error) {
        input_props.error = true;
        input_props.helperText = error;
    }
    return (<TextField
        type={show_password ? "text" : "password"}
        {...other_props}
        {...input_props}
        fullWidth
        required
        style={{ marginTop: 5, marginBottom: 5 }}
        InputProps={{
            endAdornment: <InputAdornment position="end" >
                <IconButton onClick={x => switchShowPassword(!show_password)}>
                    {show_password ? <Visibility /> : <VisibilityOff />}
                </IconButton>
            </InputAdornment>
        }}
    />);
}


function RolToString(rol: number) {
    if (+rol === 1) return "ASESOR/A";
    if (+rol === 2) return "GUÍA";
    if (+rol === 3) return "ADMINISTRADOR/A";
    return "";
}